import svg4everybody from 'svg4everybody';
import $ from 'jquery';
import FontFaceObserver from 'FontFaceObserver';
import '../blocks/main-menu/main-menu.js'
import '../blocks/search-button/search-button.js'
import '../blocks/slider/slider.js'
import '../blocks/message/message.js'
import '../blocks/gallery/gallery.js'
import '../blocks/arrow-scroll/arrow-scroll.js'

$(() => {
    svg4everybody();
});

//fix font

var output = new FontFaceObserver('Output Sans');
var input = new FontFaceObserver('Input Mono');

output.load().then(function() {
    console.log('Загружен Output Sans');
});

input.load().then(function() {
    console.log('Загружен Input Mono');
});


